import { movePointAtAngle, scaleBetween } from 'misc/helpers'

import Point from './Point'
import Size from './Size'
import Smoothing from './Smoothing'

export default class Triangle {
  constructor (index, total) {
    this.index = index
    this.total = total
    this.scaleIndex = (this.index / (this.total - 1))

    this.a = new Point(0, 0)
    this.b = new Point(1000, 1000)
    this.c = new Point(0, 1000)

    this.center = new Point(0, 0)

    this.bounds = new Size(0, 0)

    const rSmooth = scaleBetween(index, 0, total - 1, 0.05, 0.25)
    this.rotationSmoothed = new Smoothing(rSmooth)
    this.rotation = 0
  }

  updateCenter (center, length, offset, window) {
    const x = (center.x / window.w) * length
    const y = length / 2
    const scaleIndex = this.scaleIndex

    const movementY = (center.y - (window.h / 2)) * -(0.5 - scaleIndex)

    const ax = scaleIndex * x
    const ay = (scaleIndex * y) + movementY
    this.a.setX(ax - offset.x)
    this.a.setY(ay - offset.y)

    const bx = scaleBetween(1 - scaleIndex, 0, 1, x, length)
    const by = scaleBetween(1 - scaleIndex, 0, 1, y, length) + movementY
    this.b.setX(bx - offset.x)
    this.b.setY(by - offset.y)

    const cx = scaleBetween(scaleIndex, 0, 1, 0, x)
    const cy = (length - scaleIndex * y) + movementY
    this.c.setX(cx - offset.x)
    this.c.setY(cy - offset.y)

    const centerX = (this.a.x + this.b.x + this.c.x) / 3
    const centerY = (this.a.y + this.b.y + this.c.y) / 3
    this.center.setX(centerX)
    this.center.setY(centerY)
  }

  updateRotation (rotation) {
    this.rotation = this.rotationSmoothed.next(rotation)
  }

  updateBounds (size) {
    this.bounds = size
  }
}
