import { TOTAL_TRIANGLES } from 'misc/settings'

import Triangle from './Triangle'
import MouseHandler from './MouseHandler'
import Point from './Point'
import Size from './Size'
import Smoothing from './Smoothing'

const ROTATION_STEP = 360 / 16

export default class Scene {
  constructor (canvas, debug) {
    this.debug = debug
    this.canvas = canvas
    this.context = this.canvas.getContext('2d')
    this.timeLast = null
    this.timeDelta = 0

    this.stepCounter = 0

    this.mouse = new MouseHandler(this.canvas)
    this.size = new Size(0, 0)
    this.rotation = 0
    this.interactionStep = 0
    this.triangles = []

    this.sideLength = 0
    this.contextTranslate = new Point()

    this.clickInput = new Smoothing(0.02)

    this.init()
  }

  init () {
    for (let i = 0; i < TOTAL_TRIANGLES; i++) {
      this.triangles.push(new Triangle(i, TOTAL_TRIANGLES))
    }

    this.attachEventListeners()
    this.setDimensions()
    this.updateBounds()
    this.frame()
  }

  setDimensions () {
    this.canvas.style.width = window.innerWidth + 'px'
    this.canvas.style.height = window.innerHeight + 'px'

    this.size.w = this.canvas.offsetWidth
    this.size.h = this.canvas.offsetHeight

    this.canvas.width = this.size.w
    this.canvas.height = this.size.h

    this.sideLength = Math.max(this.size.w, this.size.h)

    if (this.size.w > this.size.h) {
      this.contextTranslate.setX(0)
      this.contextTranslate.setY((this.size.w - this.size.h) / 2)
    } else {
      this.contextTranslate.setX((this.size.h - this.size.w) / 2)
      this.contextTranslate.setY(0)
    }
  }

  // setDimensions () {
  //   this.canvas.style.width = '1000px'
  //   this.canvas.style.height = '1000px'
  //
  //   this.size.w = '1000'
  //   this.size.h = '1000'
  //
  //   this.canvas.width = this.size.w
  //   this.canvas.height = this.size.h
  // }

  updateBounds () {
    this.triangles.forEach(triangle => {
      triangle.updateBounds(this.size)
    })
  }

  attachEventListeners() {
    this.canvas.addEventListener('click', () => {
      this.rotation -= ROTATION_STEP
      this.clickInput.next(1)
    })
    window.addEventListener('debouncedResize', () => {
      this.setDimensions()
      this.updateBounds()
    })
  }

  frame (timestamp) {
    if (!this.timeLast) this.timeLast = timestamp

    let delta = timestamp - this.timeLast
    this.timeDelta = delta / 1000
    this.timeLast = timestamp

    if (this.timeDelta) {
      this.stepCounter = this.stepCounter + this.timeDelta
    }

    this.mouse.step()

    if (Math.abs(this.mouse.getAndResetVelocity()) > 8) {
      this.rotation -= ROTATION_STEP / 2
    }

    const clickInput = this.clickInput.next(0)
    console.log(clickInput)

    let context = this.context
    context.clearRect(0, 0, this.size.w, this.size.h)

    const s = 100

    this.triangles.forEach((t, index) => {
      context.save()
      t.updateCenter(this.mouse.getPosition(), this.sideLength, this.contextTranslate, this.size)
      t.updateRotation(this.rotation)

      context.translate(t.center.x, t.center.y)
      context.rotate(t.rotation * Math.PI / 180)
      context.translate(-t.center.x, -t.center.y)

      context.beginPath()
      context.moveTo(t.a.x, t.a.y)
      context.lineTo(t.b.x, t.b.y)
      context.lineTo(t.c.x, t.c.y)
      context.closePath()

      const h = (14 * this.stepCounter + (40 * t.scaleIndex)) % 360
      const l = ((1 - (t.rotation % (ROTATION_STEP / 6)) / ROTATION_STEP) * 10) + 50
      if (index === 0) {
        // console.log(l)
      }

      context.lineWidth = 1.5
      context.strokeStyle = `hsl(${h}, ${s}%, ${l}%)`

      context.stroke()

      context.restore()
    })

    window.requestAnimationFrame((timestamp) => {
      this.frame(timestamp)
    })
  }
}
