export default class Smoothing {
  constructor (smoothing = 0.08) {
    this.prev = 0

    this.smoothing = smoothing
  }

  next (input, force) {
    if (force) {
      this.prev = input
      return input
    }

    if (Math.abs(input - this.prev) > 0.003) {
      const value = this.prev + this.smoothing * (input - this.prev)
      this.prev = value

      return value
    } else {
      this.prev = input
      return this.prev
    }
  }
}

