import Point from "../classes/Point";
import Size from "../classes/Size";


export function scaleBetween(unscaledNum, min, max, minAllowed, maxAllowed) {
  return Math.min(Math.max((maxAllowed - minAllowed) * (unscaledNum - min) / (max - min) + minAllowed,minAllowed),maxAllowed);
}

/**
 * Converts degrees to radians.
 * 
 * @param {Number} degrees The value in degrees.
 */
export function degToRadians(degrees) {
  return degrees * Math.PI / 180;
}

/**
 * Moves a point at an angle and distance.
 * 
 * @param {Point} point The point to move
 * @param {Number} angle The angle in degress
 * @param {Number} distance How much to move the point in pixels
 * @param {Size} bounds Boundaries so the point is not moved outside this area
 */
export function movePointAtAngle(point, angle, distance, bounds) {
  const radians = degToRadians(angle)

  let x = point.x + (Math.sin(radians) * distance)
  let y = point.y - (Math.cos(radians) * distance)

  if (bounds) {
    x = Math.max(Math.min(x, bounds.w), 0)
    y = Math.max(Math.min(y, bounds.h), 0)
  }

  return { x, y }
}

/**
 * 
 * @param {String} type The name of the event to debounce
 * @param {String} name The new name of the debounced event
 * @param {Object} obj Where to trigger the events
 */
export function debouncer(type, name, obj) {
  obj = obj || window
  let running = false
  let func = function () {
    if (running) { return; }
    running = true
    requestAnimationFrame(function () {
      obj.dispatchEvent(new CustomEvent(name))
      running = false
    })
  }
  obj.addEventListener(type, func)
}