import { scaleBetween } from 'misc/helpers'

import Point from './Point'
import Smoothing from './Smoothing'

export default class MouseHandler {
  constructor (target) {
    this.target = target

    this.timeBefore = 0
    this.pointNow = new Point(0, 0)
    this.pointBefore = new Point(0, 0)
    this._velocity = 0
    this._rawVelocity = 0

    this._smoothX = new Smoothing(0.13)
    this._smoothY = new Smoothing(0.13)

    this.pointSmoothed = new Point(0, 0)

    this.init()
  }

  init () {
    this.target.addEventListener('mousemove', (e) => this.handleMouseMove(e))
  }

  handleMouseMove (e) {
    this.pointNow.setPoint({ x: e.clientX, y: e.clientY })

    const timeNow = Date.now()
    const interval = timeNow - this.timeBefore

    const diffX = this.pointNow.x - this.pointBefore.x
    //const diffY = this.pointNow.y - this.pointBefore.y

    //let sum = diffX * diffX + diffY * diffY
    let sum = diffX * diffX

    let newVelocity = (Math.sqrt(sum) / interval) * 100

    if (diffX < 0) {
      newVelocity = newVelocity * -1
    }

    this._rawVelocity = scaleBetween(newVelocity, -500, 500, -10, 10)

    this.pointBefore.setPoint(this.pointNow)
    this.timeBefore = timeNow
  }

  step () {
    const smoothedX = this._smoothX.next(this.pointNow.x)
    const smoothedY = this._smoothY.next(this.pointNow.y)

    this.pointSmoothed.setPoint({ x: smoothedX, y: smoothedY })
  }

  getPosition () {
    return {
      x: this.pointSmoothed.x,
      y: this.pointSmoothed.y
    }
  }

  getAndResetVelocity () {
    const velocity = this._rawVelocity
    this._rawVelocity = 0
    return velocity
  }
}
